import "./search_transaction.css";
import Button from "../../ui/Button/Button";
import { useEffect, useRef, useState } from "react";
import Input from "../../ui/Input/Input";
import clientAxios from "../../api/clientAxios";
import responseToast from "../../util/response";
import Toast from "../../ui/Toast/Toast";
import { ToastContainer, toast } from "react-toastify";

const SearchTransaction = () => {
  const searchRef = useRef(null);
  const [data, setData] = useState({});
  const queryParam = new URLSearchParams(window.location.search);
  const transaction_id = queryParam.get("transaction");

  useEffect(async () => {
    try {
      if (transaction_id) {
        searchRef.current.scrollIntoView({ behavior: "smooth" });
        const response = await clientAxios.get(
          "/transaction/get_info/" + transaction_id
        );
        setData(response.result);
      }
    } catch (error) {
      responseToast(error, toast);
    }
  }, []);

  return (
    <div className="search_transaction">
      <div className="hero">
        <div className="hero_content">
          <h1>Magic Post</h1>
          <p>
            Magic Post l mt n v vn chuyn hng u ti Vit Nam, chng ti
            cung cp dch v giao hng nhanh chng, ng tin cy v tit kim,
            cung cp thng tin tc th v tnh trng v v tr ca cc gi hng.
          </p>
          <Button
            text="Tra cu n hng"
            onClick={() => {
              searchRef.current.scrollIntoView({ behavior: "smooth" });
            }}
            className={"action"}
          />
        </div>
      </div>
      <div className="search_content" id="search_content" ref={searchRef}>
        <h2>Tra cu n hng</h2>
        <Input
          type="text"
          placeholder="Nhp m n hng"
          className="search_input"
          id="search_input"
        />
        <Button
          text="Tra cu"
          className="submit"
          onClick={() => {
            window.location.href =
              "/view/transaction/?transaction=" +
              document.getElementById("search_input").value;
          }}
        />
      </div>
      <div className="search_result">
        {/* {data && <p>{JSON.stringify(data)}</p>} */}
        {data?.status?.map((status, index) => (
          <div className="search_result_item" key={index}>
            <div className="search_result_item_left">
              <h3>{status.status}</h3>
              <p>{new Date(status.date).toLocaleString()}</p>
            </div>
            <div className="search_result_item_right">
              <p>{status.location}</p>
            </div>
          </div>
        ))}
      </div>
      <div className="footer">
        <div className="footer_content">
          <div className="footer_content_left">
            <h3>Lin h</h3>
            <p>S in thoi: 1900 1234</p>
            <a href="mailto: magicpost.uet@gmail.com">
              Email: magicpost.uet@gmail.com
            </a>
            <p>a ch: 144 Xun Thy, Cu Giy, H Ni</p>
          </div>
          <div className="footer_content_right">
            <h3>Lin kt</h3>
          </div>
        </div>
      </div>
      <ToastContainer />
    </div>
  );
};

export default SearchTransaction;
